var selectedAnswer1 = null;
var selectedAnswer2 = null;
var correctAnswer1 = "Florida";
var correctAnswer2 = "Arizona";
var allOptions;
var allOptions2;
var score = 0;
document.addEventListener("DOMContentLoaded", () => {
  document.querySelector(".startBtn").addEventListener("click", () => {
    clickOnStartFn();
  });

  //Options Click Handler
  allOptions = document.querySelectorAll(".optQues1");
  for (let i = 0; i < allOptions.length; i++) {
    allOptions[i].addEventListener("click", (e) => {
      getAnswer1(allOptions[i]);
    });
  }

  //Options 2 Click Handler
  allOptions2 = document.querySelectorAll(".optQues2");
  for (let i = 0; i < allOptions2.length; i++) {
    allOptions2[i].addEventListener("click", (e) => {
      getAnswer2(allOptions2[i]);
    });
  }

  document
    .querySelector(".secondPage_NextBtn")
    .addEventListener("click", () => {
      goToNextQuestion();
    });

  document
    .querySelector(".thirdPage_SubmitBtn")
    .addEventListener("click", () => {
      goToResultPage();
    });
});

function clickOnStartFn() {
  document.querySelector(".firstPage").style.transform = "translateY(-100%)";
  document.querySelector(".firstPage").style.opacity = "0";
  setTimeout(() => {
    document.querySelector(".firstPage").style.display = "none";
    document.querySelector(".secondPage").style.opacity = "1";
  }, 800);
}
function getAnswer1(elem) {
  for (let i = 0; i < allOptions.length; i++) {
    // allOptions[i].children[0].src = "../assets/Asset_20.png";
    allOptions[i].children[0].src =
      "https://abhishek3830.github.io/webDev/assets/Asset_20.png";
  }
  // elem.children[0].src = "../assets/Asset_15.png";
  elem.children[0].src =
    "https://abhishek3830.github.io/webDev/assets/Asset_15.png";
  selectedAnswer1 = elem.dataset.optval;
  console.log(selectedAnswer1, "mmk");
}

function goToNextQuestion() {
  document.querySelector(".secondPage").style.transform = "translateY(-100%)";
  document.querySelector(".secondPage").style.opacity = "0";
  setTimeout(() => {
    document.querySelector(".secondPage").style.display = "none";
    document.querySelector(".thirdPage").style.opacity = "1";
  }, 800);
}

function getAnswer2(elem) {
  for (let i = 0; i < allOptions2.length; i++) {
    // allOptions2[i].children[0].src = "../assets/Asset_20.png";
    allOptions2[i].children[0].src =
      "https://abhishek3830.github.io/webDev/assets/Asset_20.png";
  }
  // elem.children[0].src = "../assets/Asset_19.png";
  elem.children[0].src =
    "https://abhishek3830.github.io/webDev/assets/Asset_19.png";
  selectedAnswer2 = elem.dataset.optval;
  console.log(selectedAnswer2, "wwwwa");
}
function goToResultPage() {
  console.log(selectedAnswer1, " ANSERR ", selectedAnswer2);
  if (selectedAnswer1 == correctAnswer1) {
    score += 1;
  }
  if (selectedAnswer2 == correctAnswer2) {
    score += 1;
  }
  document.querySelector(".scoreTxt").innerHTML = `${score}/2`;
  document.querySelector(".thirdPage").style.transform = "translateY(-100%)";
  document.querySelector(".thirdPage").style.opacity = "0";
  setTimeout(() => {
    document.querySelector(".thirdPage").style.display = "none";
    document.querySelector(".resultPage").style.opacity = "1";
  }, 800);
}
